.navbar {
    background-color: rgb(254, 254, 251);
    display: flex;
    /* justify-content: center; */

}
.navbar-heading{
    /* font-size: 3rem; */
    width: 29rem;
    /* margin-top:10px; */

}
.nav-items {
    list-style-type: none;
    display: flex;
    justify-content: center;
    padding: 2rem;
    text-transform: uppercase;
    font-family: 'Roboto', sans-serif;

}

nav ul li a {
    display: block;
    padding: 1.2rem .8rem;
    margin: .1rem 1.6rem;
    color: #9a9797;
    font-size: 1.8rem;
    font-weight: 800;
    text-transform: uppercase;
}

nav ul li a,
nav ul li a:after,
nav ul li a:before {
    transition: all .5s;
}

nav ul li a:hover {
    color: #858484;
}

nav ul li a {
    position: relative;
    z-index: 1;
}

nav ul li a:hover {
    color: #7f7e7d;
    text-decoration: none;
}

nav ul li a:after {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    width: 100%;
    height: .1rem;
    content: '.';
    color: transparent;
    /* background: #F1C40F; */
    background: #4ad9ae;
    visibility: none;
    opacity: 0;
    z-index: -1;
}

nav ul li a:hover:after {
    opacity: 1;
    visibility: visible;
    height: 100%;
}

.flex-align-right {
