}
.active .menu-outline {
    left: 100%;
}
.active .close-outline{
    display: inline-block;
}
.active .nav-items {
    visibility: visible;
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
    z-index: 999;
}


