 }





 /* The Modal (background) */
 .modal {
     display: none;
     /* Hidden by default */
     position: fixed;
     /* Stay in place */
     z-index: 1;
     /* Sit on top */
     padding-top: 100px;
     /* Location of the box */
     left: 0;
     top: 0;
     width: 100%;
     /* Full width */
     height: 100%;
     /* Full height */
     overflow: auto;
     /* Enable scroll if needed */
     background-color: rgb(0, 0, 0);
     /* Fallback color */
     background-color: rgba(0, 0, 0, 0.4);
     /* Black w/ opacity */
     padding-top: 300px;

 }

 /* Modal Content */
 .modal-content {
     background-color: #fefefe;
     margin: auto;
     padding: 20px;
     border: 1px solid #888;
     width: 600px;
     border-radius: 10px;
     box-shadow: #393c76 -1px 2px 2px 1px;
 }

 #modalText {
     padding-top: 8px;
     padding-right: 5px;
     font-size: 18px;
     font-family: 'Poppins', sans-serif;
     color: rgb(24, 23, 23);
 }

 .modal-wrong {
     border: 2px solid red;
 }

 .modal-correct {
     border: 2px solid green;
 }

 /* The Close Button */
 .close {
     color: #aaaaaa;
     float: right;
     font-size: 28px;
     font-weight: bold;
     cursor: pointer;
     transition: all 0.3s;
 }

 .close:hover {
     color: #d41111;
 }





 .linkDownload {
     position: fixed;
     background-color: #d12322;
     margin: 20px;
     padding: 10px 10px;
     left: -0px;
     border-radius: 5px;
     bottom: -0px;
     font-size: 16px;
     font-weight: 400;
     color: #fff;
     text-decoration: none;
     text-transform: capitalize;
     transition: all 0.43s ease-in-out;


 }


 .linkDownload i {
     padding-left: 7px;
 }

 .linkDownload:hover {
     text-decoration: none;
     background-color: black;
 }


